import { FirebaseServicesService } from './../../services/firebase-services.service';
import { Component, OnInit } from '@angular/core';
import { from } from 'rxjs';
import {NgForm} from '@angular/forms';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
students: any;

  constructor(public _data : FirebaseServicesService,) { }
  RegisterStudent(StudentInfo: NgForm)
{
  this._data.Register(StudentInfo.value)
}
  ngOnInit() {


    this._data.viewAllStudents().snapshotChanges().subscribe( action =>{

      console.log(action)
      this.students=action
    })
  }

}
